.wrap-msg {
  position: absolute;
  height: 100vh;
  width: 100%;
  background-color: #345757;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
}

.msg {
  display: block;
  color: #FFFFFF;
  font-size: 20px;
  width: 90%;
  word-break: break-all;
}